export const query = graphql`
  query storeBySlug($slug: String) {
    allStrapiStores(
      filter: {
        slug: { eq: $slug }
        isActive: { eq: true }
        isApproved: { eq: true }
      }
    ) {
      nodes {
        aboutStore
        aboutStoreTa
        addressFull
        addressFullTa
        businessStartedYear
        emailId
        fbUrl
        id
        instaUrl
        isApproved
        isActive
        latitude
        locationShort
        locationShortTa
        longitude
        maxAmount
        minAmount
        name
        nameTa
        phoneNo
        phoneNo1
        pincode
        placeId
        servicesOffered
        servicesOfferedTa
        slug
        twitterUrl
        updatedAt
        website
        whatsAppNo
        youtubeUrl
        categories {
          id
          label
          labelTa
          slug
        }
        locations {
          id
          label
          labelTa
          slug
        }
        gallery {
          desc
          id
          images {
            id
            image {
              childImageSharp {
                fixed(width: 125, height: 125, quality: 100) {
                  ...GatsbyImageSharpFixed_withWebp
                }
                fluid(maxWidth: 1200, quality: 100) {
                  ...GatsbyImageSharpFluid
                  srcSetWebp
                  srcWebp
                }
              }
            }
          }
        }
      }
    }
  }
`;

function getVendorSchema(store) {
  const logoImage =
    store.gallery &&
    store.gallery[0] &&
    store.gallery[0].images[0] &&
    store.gallery[0].images[0].image &&
    store.gallery[0].images[0].image.childImageSharp &&
    store.gallery[0].images[0].image.childImageSharp.fluid &&
    store.gallery[0].images[0].image.childImageSharp.fluid.src;
  // console.log(store?.galleryee?.dfdf);
  const addressSchema = {
    address: {
      "@type": "PostalAddress",
      addressLocality: store.locationShort,
      addressRegion: store.locations[0].label,
      ...(store.pincode && { postalCode: store.pincode }),
      ...(store.addressFull && { streetAddress: store.addressFull }),
    },
  };

  const serviceSchema = store.categories.map(categoryItem => {
    return {
      "@context": "http://schema.org/",
      "@type": "Service",
      serviceType: categoryItem.label.includes("wedding")
        ? categoryItem.label
        : `Wedding ${categoryItem.label}`,
      provider: {
        "@type": "LocalBusiness",
        name: store.name,
        ...(logoImage && { image: logoImage }),
      },
      areaServed: {
        "@type": "Place",
        ...addressSchema,
      },
    };
  });

  const LocalBusinessSchema = {
    "@context": "http://schema.org",
    "@type": "LocalBusiness",
    ...addressSchema,
    description: store.aboutStore,
    name: store.name,
    ...(logoImage && { image: logoImage }),
    ...(store.website && { url: store.website }),
    ...(store.phoneNo1 && { telephone: store.phoneNo1 }),
    ...(store.longitude &&
      store.latitude && {
        geo: {
          "@type": "GeoCoordinates",
          latitude: store.latitude,
          longitude: store.longitude,
        },
      }),
  };

  return [...serviceSchema, LocalBusinessSchema];
}

export default injectIntl(VendorMainPage);
